import "./index.scss"
import React from "react"
import { useEffect, useState } from "react"
import { Loader } from "./components/Loader/Loader"
import { Header } from "./components/Header/Header"
import { Main } from "./components/Main/Main"
import { Skills } from "./components/skills/Skills"
import { Projects } from "./components/Projects/Projects"
import axios from "axios"
import Github from "./components/Github/Github"

export const LocaleContext = React.createContext()

function App() {
	const [locale, setLoc] = useState(JSON.parse(localStorage.getItem("locale")) || "en")

	const [projects, setProjects] = useState([])
	const [skills, setSkills] = useState([])
	const [alsoKnow, setAlsoKnow] = useState([])

	function setLocale(locale) {
		setLoc(locale)
		localStorage.setItem("locale", JSON.stringify(locale))
	}

	async function fetchProjects() {
		let data = await axios.get("./projects.json")
		setProjects(data.data)
	}

	async function fetchSkills() {
		const data = await axios.get("./skills.json")
		setSkills(data.data.skills)
		setAlsoKnow(data.data.alsoKnow)
	}

	useEffect(() => {
		const white = JSON.parse(localStorage.getItem("theme"))
		if (white) document.documentElement.classList.add("white")
		fetchProjects()
		fetchSkills()
	}, [])

	return (
		<>
			<LocaleContext.Provider value={{ locale: locale, setLocale: setLocale }}>
				<Loader />

				<div className="app">
					<Header />
					<Main />

					<div className="skills_container">
						<Skills title={{ en: "Skills", ua: "" }} skills={skills} />
						<Skills className="also_know" title={{ en: "Also know", ua: " " }} skills={alsoKnow} />
					</div>

					<Github />

					<Projects projects={projects} setProjects={setProjects} />
				</div>
			</LocaleContext.Provider>
		</>
	)
}

export default App
