import { Octokit } from "octokit";

const octokit = new Octokit({ 
	auth: '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', 
	baseUrl: 'https://api.github.com'
});

export default class GithubAPI {
	static async getReposAmount() {
		const response = await octokit.request('GET /user/repos')
		return response.data.length || 0;
	}

	static async getCommits() {
		const responseRepos = await octokit.request('GET /user/repos')
		const repos = responseRepos.data;

		const array = repos.map(async (repo) => {
			const response = await octokit.request(`GET /repos/${repo.full_name}/commits`)
			return response.data;
		});

		const values = await Promise.all(array)
		
		const result = [];
		values.forEach(value => result.push(...value))
		
		const ret = result.filter( commit => commit.author.login === "ElRataAlada")

		return (ret.length + repos.length) || 0;
	}
}