import { useContext, useMemo, useState } from 'react'
import { useAutoAnimate } from '@formkit/auto-animate/react'
import { LocaleContext } from '../../App'
import { Filter } from './Filter/Filter'
import { Project } from './Project/Project'
import './Projects.scss'

const selectors = [{en:"all", ua:""}, {en:"react", ua:"react"}, {en:"markup", ua:""}]

export function Projects ({projects}){
    const {locale} = useContext(LocaleContext)

    const [selectorId, setSelectorId] = useState(0);
    const [parent] = useAutoAnimate()

    const sortedProjects = useMemo(() => filterProjects(selectors[selectorId].en) , [projects, selectorId])

    function filterProjects(query=""){
        if(query === 'all') return projects      
        else if (query === 'react') return projects.filter(project => project["framework"] === 'react')
        else if (query === 'markup') return projects.filter(project => !project["framework"])
    }

    return(
        <div className="projects_wrapper no-select">

            {locale === 'ua' ?
                <h2 className='projects-title'></h2>:
                <h2 className='projects-title'>Projects</h2>
            }

            <Filter selectors={selectors} setSelectorId={setSelectorId} selectorId={selectorId}/>

            <div>
                {projects.length === 0 ? locale === 'ua' ?
                    <h3 className='empty'>      React . . .</h3>:
                    <h3 className='empty'>Only this site is made using React . . .</h3>:
                    <></>
                } 

                <div ref={parent} className='projects'>
                    {sortedProjects?.map(project => <Project key={project.name} data={project}/>)}
                </div>

            </div>
        </div>
    )
}